    @media (max-width: 768px) {
      .cta-top__img {
        width: 100%;
        background-image: linear-gradient(to left, #ffffff00 15%, #29303b 40%), url(/ecommerce1.0/static/media/vipansh-pic.6b5f7c09.jpg);
        background-repeat: inherit; } }
    @media (max-width: 596px) {
