.header {
  display: flex;
  position: relative;
  justify-content: space-between;
  width: 100vw;
  height: 7rem;
  align-items: center;
  box-shadow: 0 0.3rem 0.6rem rgba(0, 0, 0, 0.3);
  overflow-x: revert; }
  .header__left {
    width: 30%; }
    .header__left--img {
      overflow-x: hidden;
      display: flex;
      width: 90%;
      cursor: pointer; }
  .header__center {
    display: flex;
    width: 40%;
    justify-content: center; }
  .header__right {
    width: 30%;
    display: flex;
    justify-content: flex-end; }
    .header__right--cart {
      display: flex;
      height: 3rem;
      width: 3rem;
      cursor: pointer;
      align-self: center;
      margin-right: 0.5rem; }
    .header__right--login {
      height: 4rem;
      width: 12rem;
      font-weight: 600;
      border-radius: 10rem;
      color: #f7f7f7;
      background-image: linear-gradient(to left, #12658654, #5cb7db);
      border-radius: 10rem;
      border: none;
      outline: none;
      cursor: pointer;
      transition: all 0.5s; }
      .header__right--login:hover {
        color: #5cb7db;
        background-image: linear-gradient(to left, #f7f7f7, #f7f7f763);
        border: 2px solid #5cb7db; }

.search {
  width: 28rem;
  height: 3rem;
  border: 2px solid #000000;
  border-radius: 10rem;
  outline: none; }
  .search::-webkit-input-placeholder {
    padding-left: 4rem;
    font-size: 1.5rem; }
  .search:-ms-input-placeholder {
    padding-left: 4rem;
    font-size: 1.5rem; }
  .search::-ms-input-placeholder {
    padding-left: 4rem;
    font-size: 1.5rem; }
  .search::placeholder {
    padding-left: 4rem;
    font-size: 1.5rem; }
  .search:focus {
    border: 2px solid #5cb7db;
    padding-left: 4rem; }
    .search:focus::-webkit-input-placeholder {
      color: #ffffff00; }
    .search:focus:-ms-input-placeholder {
      color: #ffffff00; }
    .search:focus::-ms-input-placeholder {
      color: #ffffff00; }
    .search:focus::placeholder {
      color: #ffffff00; }
  .search__icon {
    position: absolute;
    height: 2rem;
    width: 2rem;
    transform: translate(1rem, 0.5rem); }
  @media (max-width: 998px) {
    .search {
      width: 25rem; } }
  @media (max-width: 768px) {
    .search {
      width: 20rem; } }
  @media (max-width: 596px) {
    .search {
      width: 15rem; } }

.cource-slider {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 4rem; }
  .cource-slider__title {
    display: block;
    text-align: center;
    font-size: 3rem; }
  .cource-slider__undeliner {
